import Vue from 'vue'
import Router from 'vue-router'
import App from '../App'


// import mainMsg  from '../pages/main/msg.vue'
// const mainWork = r => require.ensure([], () => r(require('../pages/index.vue')))
const mainWork = r => require.ensure([], () => r(require('../pages/main/index.vue')),'mainWork')
// 搜索页面
const mainSearch = r => require.ensure([], () => r(require('../pages/main/search.vue')))
const search = r => require.ensure([], () => r(require('../components/main/search.vue')))
const comprehensive_search = r => require.ensure([], () => r(require('../pages/main/search/contractSearch')),"comprehensive_search")
const keywordSearch = r => require.ensure([], () => r(require('../pages/main/search/keywordSearch')),"keywordSearch")
//消息中子页面 好友
const msgNewFriend = r => require.ensure([], () => r(require('../pages/main/msg_newFriend.vue')))
const dingMi = r => require.ensure([], () => r(require('../pages/main/msg_dingMI.vue')))
const dingVersion = r => require.ensure([], () => r(require('../pages/main/msg_dingNewVersions.vue')))
const dingMail = r => require.ensure([], () => r(require('../pages/main/msg_dingMail.vue')))
const notice = r => require.ensure([], () => r(require('../pages/main/msg_notice.vue')))
//联系人创建团队
const creatTeam = r => require.ensure([], () => r(require('../pages/main/cont_team.vue')))
// 我的
const umesg = r => require.ensure([], () => r(require('../pages/main/my_msg')))
const myteam = r => require.ensure([], () => r(require('../pages/main/my_team')))
// 工作流搜索
const workFlow = r => require.ensure([], () => r(require('../pages/main/work_flow')),'workFlow')
const workNotice = r => require.ensure([], () => r(require('../pages/main/work_notice')),'workNotice')
const contract = r => require.ensure([], () => r(require('../pages/main/work_add/contract')),'contract')
//群聊 添加好友（加号图标下拉框）
const groupChat = r => require.ensure([],() => r(require('../pages/main/addContact/group_chat')),'groupChat')
const creatGroupMSg = r => require.ensure([],() => r(require('../pages/main/addContact/creat_group_msg')),'creatGroupMSg')
const removeSelectedCont = r => require.ensure([],() => r(require('../pages/main/addContact/removeSelectedCont')),'removeSelectedCont')
const groupChatPage = r => require.ensure([],() => r(require('../pages/main/addContact/groupChatPage')),'groupChatPage')
const groupMsgDetail = r => require.ensure([],() => r(require('../pages/main/addContact/groupMsgDetail')),'groupMsgDetail')


// appStore的路由
const AppStore = r => require.ensure([], () => r(require('../pages/appStore/index')),'appStore')
const appList = r => require.ensure([], () => r(require('../pages/appStore/appList')),'appList')
const appDetail = r => require.ensure([], () => r(require('../pages/appStore/appDetail')),'appDetail')
const routerChild = r => require.ensure([], () => r(require('../components/routerChild')),'routerChild')


Vue.use(Router)

const routes = [{

  path:'/',component:App,
  children:[
    { path: "",redirect:"/mainWork" },
    { path: "/mainWork",component: mainWork},
    //search 路由
    { path: "/mainSearch",component: mainSearch},
    { path: "/search",component: search},
    { path: "/comphSearch",component: comprehensive_search},
    { path: "/keywordSearch",component: keywordSearch},

    { path: "/workFlow",component: workFlow},
    { path: "/workNotice",component: workNotice},
    // 新增
    {
      path:'/add',component:routerChild,
      children:[
        {path:'contract',component:contract}
      ]
    },
    // 联系人 创建团队
    { path: "/cont",component:routerChild,
      children:[
        {path:'',component:creatTeam},
        {path:'creatTeam',component:creatTeam}
      ]
    },
    //群聊 添加好友 路由
    { path: "/groupChat", component: groupChat},
    { path: "/creatGroupMSg", component: creatGroupMSg},
    { path: "/removeSelectedCont", component: removeSelectedCont},
    { path: "/groupChatPage", component: groupChatPage},
    { path: "/groupMsgDetail", component: groupMsgDetail},

    //我的页面 的子路由
    { path: "/mainMy",component:routerChild,
      children:[
        {path:'/',component:umesg},
        {path:'umesg',component:umesg},
        {path:'myteam',component:myteam}
      ]
    },
    // 消息的路由
    { path:"/msgNewFriend", component: msgNewFriend },
    { path: "/dingMi", component: dingMi},
    { path: "/dingVersion", component: dingVersion},
    { path: "/dingMail", component: dingMail},
    { path: "/notice", component: notice},

    //appstore 路由
    { path: "/appstore",component: AppStore},
    { path: "/applist", component: appList },
    { path: "/appDetail", component: appDetail }
  ]
}]

export default new Router({
    routes,linkActiveClass:"my-active"
})



