import {ROUTER_QUERY} from './mutation-types'

export default{
  [ROUTER_QUERY](state,query){
    state.count+=query;
  },
  updateSearchParams (state,query){
    state.searchParams=query;
    console.log("VUEX=>",state.searchParams);
  },
  toggleSelectedContacts(state,val){
    // var contactsIndex=state.contactsSelected.indexOf(val)
    for (var i = 0; i < state.contactsSelected.length; i++) {
      if(val.id==state.contactsSelected[i].id){
        state.contactsSelected.splice(i,1)
        return
        // console.log("cunzai");
      }
    }
    state.contactsSelected.push(val)
    return
  },
  initSelectedContacts(state){
    state.contactsSelected=[{
      name:"我自己",
      title:'JAVA',
      imgSrc:"../../../static/img/icon/service.png",
      id:10
    }]
    console.log(state.contactsSelected)
  },
  updateGroupMsg(state,val){
    state.groupMsg=val
    console.log(state.groupMsg)
  },
  addGroupList(state,val){
    state.groupList.push(val)
  }
}



